.hiddenInput{
    opacity: 0;
    line-height: 0;
    height: 0;
    width: 0;
    overflow: hidden;
    padding: 0;
    margin: 0;
  }
  .filePicker{
    width: 50px;
  }
  
  .callModalWindowsStyle{
    display: flex;
    justify-content: center;
    margin-top: 4px;
  }
  
  .button{
    margin-left: 4px;
    font-family: 'Raleway', sans-serif;
    font-size: 15px;
    color: #fff;
    background-color: rgb(0, 78, 154, 1);
    border: none;
    border-radius: 15px;
    cursor: pointer;
    padding: 15px;
  }
  
  .container{
    display: flex;
    align-items: center;
  }
  
  .button:hover{
    color: chocolate;
  }