  * {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
  }
   
  /* Tab Container */
  .Tabs {
    width: 80%;
    height: auto;
    min-height: 400px;
    background: #053742;
    margin: 3.5rem auto 1.5rem;
    padding: 2rem 1rem;
    color: #E8F0F2;
    border-radius: 2rem;
    @media (max-width: 769px) {
      padding: 2rem 0;
    }
   
  }
   
  /* Tab Navigation */
  ul.nav {
    width: 60%;
    margin: 0 auto 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 1px solid #39A2DB;
    border-radius: 2rem;
    padding-left: 0px;
    @media (max-width: 768px) {
      width: 90%;
    }
  }
   
  ul.nav li {
    width: 50%;
    padding: 1rem;
    list-style: none;
    text-align: center;
    cursor: pointer;
    transition: all 0.7s;
  }
   
  ul.nav li:first-child {
    border-bottom-left-radius: 2rem;
    border-top-left-radius: 2rem;
  }
   
  ul.nav li:last-child {
    border-bottom-right-radius: 2rem;
    border-top-right-radius: 2rem;
  }
   
  ul.nav li:hover {
    background: rgba(50, 224, 196, 0.15);
  }
   
  ul.nav li.active {
    background: chocolate;
  }
   
  /* Tab Content Styles */
  .TabContent {
    font-size: 1rem;
    text-align: center;
  }

  .in{
    width:2rem;
    font-size: 1rem;
    text-align: center;
  }

  .button{
    margin-left: 4px;
    font-family: 'Raleway', sans-serif;
    font-size: 15px;
    color: #fff;
    background-color: rgb(0, 78, 154, 1);
    border: none;
    border-radius: 15px;
    cursor: pointer;
    padding: 15px;
  }

  .buttonContainer{
    display: flex;
    justify-content: flex-end;
  }

  .button:hover{
    color: chocolate;
  }

  .additionalInformation{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .finalButtons{
    width: 80%;
    display: flex;
    justify-content: space-around;
  }

  .additionalInformationCheckBox{
    display: flex;
    justify-content: space-evenly;
  }

  .EOrBFieldCheckBox{
    display: flex;
    justify-content: space-evenly;
  }

  .EOrBFieldCheckBox input{
    margin-right: 8px;
  }

  .additionalInformationCheckBox input{
    margin-right: 8px;
  }

  .activeButton{
    border: none;
    color: #E8F0F2;
    background: chocolate;
  }

  .navButton{
    border: none;
    color: #E8F0F2;
    background: #053742;
  }

  
  .navButton:hover{
    border: none;
    color: #E8F0F2;
    background: rgba(50, 224, 196, 0.15);
  }

  .container{
    padding-top: 24px;
    display: flex;
    flex-direction: column;
  }

  .buttonsContainer{
    display: flex;
    justify-content: center;
  }